import React, { createContext, useContext, useEffect, useState } from 'react';

import { TabProps } from '../Tab/Tab';

import styles from './Tabs.module.scss';

const TabsContext = createContext({
    activeTab: '',
    setActiveTab: (id: string) => {
        id;
    },
});

export interface TabsProps {
    children: React.ReactElement<TabProps> | React.ReactElement<TabProps>[];
    onChange?: (value: string) => void;
    defaultId?: string;
}

const Tabs = ({ children, onChange, defaultId }: TabsProps) => {
    const [activeTab, setActiveTab] = useState(defaultId || '');

    useEffect(() => {
        if (!defaultId && React.Children.count(children) > 0) {
            const firstChild = React.Children.toArray(
                children
            )[0] as React.ReactElement<TabProps>;
            setActiveTab(firstChild.props.id);
            onChange && onChange(firstChild.props.id);
        }
    }, []);

    const handleChange = (value: string) => {
        setActiveTab(value);
        onChange && onChange(value);
    };

    return (
        <TabsContext.Provider value={{ activeTab, setActiveTab: handleChange }}>
            <div className={styles.tabs} role="tablist">
                {children}
            </div>
        </TabsContext.Provider>
    );
};

export const useTabsContext = () => useContext(TabsContext);

export default Tabs;
